import React from 'react';
import '../../css/user/user.css';
import { Form, Button, Col, Container, Row } from 'react-bootstrap';
function Register(){
    return(
        <Container className="mt-4">
            <Row>
                <Col md={{ span: 6, offset: 3 }}>
                    <h2>회원가입</h2>
                    <Form>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Email 주소</Form.Label>
                            <Form.Control className="smaller-input" type="email" placeholder="이메일을 입력하세요" />
                            <Button variant="danger">인증번호 전송</Button>{' '}
                        </Form.Group>

                        <Form.Group controlId="formBasicVerificationCode">
                            <Form.Label>인증번호</Form.Label>
                            <Form.Control className="smaller-input" type="text" placeholder="인증번호를 입력하세요" />
                            <Button variant="danger">인증번호 확인</Button>{' '}
                        </Form.Group>

                        <Form.Group controlId="formBasicPassword">
                            <Form.Label>비밀번호</Form.Label>
                            <Form.Control type="password" placeholder="비밀번호를 입력하세요" />
                        </Form.Group>

                        <Form.Group controlId="formBasicPasswordConfirm">
                            <Form.Label>비밀번호 확인</Form.Label>
                            <Form.Control type="password" placeholder="비밀번호 확인" />
                        </Form.Group>

                        <Form.Group controlId="formBasicPhoneNumber">
                            <Form.Label>전화번호</Form.Label>
                            <Form.Control type="tel" placeholder="전화번호를 입력하세요" />
                        </Form.Group>

                        <Form.Group controlId="formBasicAddress">
                            <Form.Label>주소</Form.Label>
                            <Form.Control className="smaller-input" type="text" placeholder="우편번호 입력" />
                            <Button variant="danger">우편번호 찾기</Button>{' '}
                            <Form.Control type="text" placeholder="주소" />
                            <Form.Control type="text" placeholder="상세주소 입력" />
                        </Form.Group>

                        <div className="text-center">
                            <Button variant="danger" type="submit">
                                회원가입
                            </Button>
                        </div>

                    </Form>
                </Col>
            </Row>
        </Container>
    );
}

export default  Register;